@import '../base.scss';

.c-image-text-dbl {
  @include t-v-gutters(true);

  &__pretitle {
    @include t-gutters;
    margin-bottom: t-to-rem(6);
    text-align: center;
    color: $s-color--font-1;
  }

  &__title {
    margin-bottom: t-to-rem(115);
    text-align: center;
    color: $s-color--font-1;
  }

  &__main-container {
    margin-top: t-to-rem(115);
    position: relative;
  }

  &__background-image {
    @include t-absolute-full;
    height: 100%;
    object-fit: cover;
  }

  &__image {
    @include t-absolute-full;
    object-fit: cover;
    height: 100%;

    &-wrapper {
      position: absolute;
      overflow: hidden;
      z-index: 2;
      opacity: 0;
      transition: all 3s $s-anim--ease-out;
      transition-property: transform, opacity;

      &.fade-in {
        opacity: 1;
      }
    }

    &-container {
      padding-bottom: 100%;
      overflow: hidden;
    }
  }

  .text-block {
    background-color: $s-color--background;
    color: $s-color--font-1;
    position: relative;
    z-index: 1;

    &__link {
      font-weight: $s-font--weight-bold;
    }

    &__pre-title {
      text-transform: uppercase;
    }

    &__text {
      color: $s-color--font-5;
    }
  }

  @include t-media-query($s-medium-down) {

    &__main-container {
      padding: t-to-rem(116) 0 t-to-rem(40);
    }

    &__title {
      margin-left: $s-gutter--mobile;
      margin-right: $s-gutter--mobile;
    }

    &__image {

      &-wrapper {
        width: t-to-rem(355);

        &.is-image1 {
          right: 0;
          transform: translateY(-60%) translateX(100%);

          &.fade-in {
            transform: translateY(-60%);
          }
        }

        &.is-image2 {
          left: 0;
          transform: translateY(-60%) translateX(-100%);

          &.fade-in {
            transform: translateY(-60%);
          }
        }
      }
    }

    .text-block {
      padding: t-to-rem(160px) $s-gutter--mobile t-to-rem(40);

      &.is-block1 {
        margin-right: 5.3%;
        margin-bottom: t-to-rem(228);
      }

      &.is-block2 {
        margin-left: 5.3%;
      }

      &__pre-title,
      &__heading {
        margin-bottom: t-to-rem(16);
      }

      &__text {
        margin-bottom: t-to-rem(10);
      }
    }
  }

  @include t-media-query($s-medium-up) {
    padding-top: 0;
    margin-top: 5rem;

    &__main-container {
      margin: 0 $s-gutter--medium;
      padding: 6.3% 5.5%;
    }

    &__title {
      margin-right: auto;
      margin-left: auto;
      width: 50%;
    }

    &__image {
      &-container {
        padding-bottom: 100%;
      }

      &-wrapper {
        position: absolute;
        width: 49%;

        &.is-image1 {
          right: unset;
          top: -4.9%;
          left: -8.2%;
          transform: translateX(-100%);

          &.fade-in {
            transform: translateX(0%);
          }
        }

        &.is-image2 {
          left: unset;
          bottom: -4.9%;
          right: -8.2%;
          transform: translateX(100%);

          &.fade-in {
            transform: translateX(0%);
          }
        }
      }
    }

    .text-block {
      width: 53%;
      padding: 11% 10.4%;

      &__pre-title {
        margin-bottom: t-to-rem(29);
      }

      &__heading {
        margin-bottom: t-to-rem(29);
      }

      &__text {
        margin-bottom: t-to-rem(54);
      }

      &.is-block1 {
        margin-left: auto;
        margin-bottom: 8%;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__pretitle {
      margin-bottom: t-to-rem(18);
    }

    &__title {
      margin-top: t-to-rem(20);
      margin-bottom: t-to-rem(160);
      font-size: t-to-rem(72);
    }
  }

  @include t-media-query($s-xxlarge-up) {

    &__main-container {
      max-width: t-to-rem(1720);
      margin-left: auto;
      margin-right: auto;
    }
  }
}